import { ClassValue, clsx } from 'clsx';

type VariantValue = string | number | boolean;
type ClassProp = string | undefined | null;
type VariantProps = Record<string, VariantValue>;
type VariantConfig = Record<string, Record<string, ClassProp>>;

type CVAProperties = {
    variants?: VariantConfig;
    defaultVariants?: VariantProps;
    compoundVariants?: Array<
        Record<string, any> & { class?: string; className?: string }
    >;
};

/**
 * A utility function for creating conditional class names similar to class-variance-authority
 * but implemented locally to avoid dependency issues.
 */
export function classlist(
    base: ClassValue,
    config?: CVAProperties,
): (props?: Record<string, any>) => string {
    return function (props?: Record<string, any>): string {
        const {
            variants = {},
            defaultVariants = {},
            compoundVariants = [],
        } = config || {};
        const variantClassNames: string[] = [];

        // Process variants
        if (props && variants) {
            Object.entries(props).forEach(([propName, propValue]) => {
                const stringPropValue = String(propValue);
                if (variants[propName]?.[stringPropValue]) {
                    variantClassNames.push(
                        variants[propName][stringPropValue] || '',
                    );
                }
            });
        }

        // Process default variants
        if (defaultVariants && variants) {
            Object.entries(defaultVariants).forEach(
                ([variantName, variantValue]) => {
                    if (props && typeof props[variantName] !== 'undefined') {
                        return; // Skip if variant is specified in props
                    }

                    const stringVariantValue = String(variantValue);
                    if (variants[variantName]?.[stringVariantValue]) {
                        variantClassNames.push(
                            variants[variantName][stringVariantValue] || '',
                        );
                    }
                },
            );
        }

        // Process compound variants
        if (compoundVariants) {
            compoundVariants.forEach((compoundVariant) => {
                const {
                    class: compoundClass,
                    className: compoundClassName,
                    ...conditions
                } = compoundVariant;
                const allConditionsMet = Object.entries(conditions).every(
                    ([condName, condValue]) => {
                        return (
                            props &&
                            String(props[condName]) === String(condValue)
                        );
                    },
                );

                if (allConditionsMet) {
                    const compoundClassToUse =
                        compoundClass || compoundClassName || '';
                    if (compoundClassToUse) {
                        variantClassNames.push(compoundClassToUse);
                    }
                }
            });
        }

        return cn(base, ...variantClassNames);
    };
}

/**
 * Combines class names with clsx
 */
export function cn(...inputs: ClassValue[]): string {
    return clsx(inputs);
}
